  /** ç¼å·é¡¹åï¼é»è®¤ï¼`'id'` */
  idMapName?: string;
  /** ç¶ç¼å·é¡¹åï¼é»è®¤ï¼`'parent_id'` */
  parentIdMapName?: string;
  /** å­é¡¹åï¼é»è®¤ï¼`'children'` */
  childrenMapName?: string;
  /** è½¬æ¢ææ æ°æ®æ¶åè° */
